import React, { Component } from 'react';
import CardList from '../components/CardList';
import SearchBox from '../components/SearchBox';
import Scroll from '../components/Scroll'
import { robots } from '../components/robots';
// import ErrorBoundary from '../components/ErrorBoundary'
import './App.css';


// This is where all the window is set-up.
// Since with react components you can return only one thing at a time, it is
// useful to articulate the web app inside a <div>.


class App extends Component {
	constructor() {
		super()
		this.state = {
			//Our state is robors and searchfield, is what describes our app.
			//State is something that can change and affect our app.
			//In fact we want a reponsive search.
			robots: [],
			searchfield: '',
			//This is the virtual DOM of React. React than uses these states
			//to render and pass them down as props to the latter components.
		}
	}

	componentDidMount () {
		fetch('https://jsonplaceholder.typicode.com/users')
			.then(response => {
				return response.json();
			})
			.then(users => {
				this.setState({ robots: users});
			})
	}

	onSearchChange = (event) => {
		//updating the searchfield variable with what is written inside it
		this.setState({searchfield: event.target.value});
	}

	render(){
		//creating a variable where we filter the robot array with what is
		//written inside the searchfield.
		// With the information that we have from the searchbox we can
		// communicate to the card list and filter accordingly.
		const filteredRobots = this.state.robots.filter(robot => {
			return robot.name.toLowerCase().includes(this.state.searchfield.toLowerCase());
		})

		if (this.state.robots.length === 0) {
			return <h1 className='tc'>Loading</h1>
		} else {
			return (
				<div className='tc'>
					<h1 className='f1'>RoboFriends</h1>
					<SearchBox searchChange = {this.onSearchChange}/>
					<Scroll>
						<CardList robots= {filteredRobots}/>
					</Scroll>
				</div>
			);
		}
	}
}


export default App;


